export default {
	data() {
		return {
			specificatbuy:false,
			biId:'',
			authKey:'',
			content:'',
			skus:'',
			assemble_skus:'',
			price_minA:'',
			price_minN:'',
			prices:'',
			priceA:'',
			buyTimes:'',
			specif:'',
			specG:'',
			normal:'',
			group:'',
			index:-1,
			agindex:-1,
			azindex:-1,
			bsku_id:'',
			cassemble_id:'',
			invite_code:'',
			bassemble_skus:'',
			assembles:'',
			coupon_id:'',
			user: true,
			userInfo: ''
		};
	},
	onLoad(e) {
		this.userInfo = uni.getStorageSync('userInfo');
		this.authKey = uni.getStorageSync('authKey')
		if (this.authKey){
			this.checkSession()
		}
		this.biId = e.id
		this.invite_code = e.invite_code
		// 处理判断sence
		if(e.scene){
			const reg = /%26/g
			// console.log(reg.test(e.scene))
			if(reg.test(e.scene)){
				// 有两个参数
				const arr = e.scene.split('%26')
				this.biId = arr[0]
				this.invite_code = arr[1]
			}else{
				// 有一个参数
				this.biId = e.scene
			}
			
			
		}
		console.log(e)
	},
	methods:{
		// 检查会话是否失效
	checkSession: function() {
			var that = this;
			uni.request({
				url: this.http + 'user/checkSession',
				method: 'POST',
				header: {
					authKey: this.authKey
				},
				data: {},
				success: res => {
					if (res.data.code == 101) {
						that.user = true
						that.authKey = ''
						uni.setStorageSync('authKey','')
					} else {
						that.user = false
						that.getListDetail()
					}
				},
			});
	},
	wxGetUserInfo:function(res){
		let that = this
		if (!res.detail.iv) {
			uni.showToast({ 
				title: "您取消了授权,登录失败", 
				icon: "none" ,
			}); 
			return false; 
		}
		let simpleUser = res.detail.userInfo
		let encryptedData = res.detail.encryptedData
		let iv = res.detail.iv
		uni.login({
			provider: 'weixin',
		    success(res){
			  console.log(res.code,'login')
			  var data = res.code
		    if (res.code) {
			  uni.request({
			  	url: that.http + 'user/token',
			  	method: 'GET',
			  	data: {
			  		login_code: res.code,
					encrypted_data:encryptedData,
					iv: iv
			  	},
			  	success: res => {
					if (res.data.code == 200) {
						let auth = res.data.data.authkey
						that.authKey = auth
						that.userInfo = simpleUser
						uni.setStorageSync('authKey',auth)
						uni.setStorageSync('userInfo',simpleUser)
						that.user = false
						
						that.getListDetail()
					} else {
						uni.showToast({
							title: res.data.error,
							icon: 'none'
						})
					}
			  		// that.haveOrder()
			  	},
			  });
		    } else {
		      console.log('登录失败！' + res.errMsg)
		    }
		  }
		})
	},
	close(e){
		this.user = false;
		uni.showToast({
			title: '未微信授权',
			icon:'none',
			mask: false,
			duration: 1500
		})
	},
		// 获取服务详情
		getListDetail(){
			var that = this;
			uni.request({
				url: this.http+'assemble/detail/' + this.biId,
				method: 'GET',
				header: {
					authKey: this.authKey
				},
				data: {},
				success: res => {
					console.log(res.data)
					if (res.data.code == 200) {
						let content = res.data.data
						content.product.cover = that.static + content.product.cover
						that.content = content
						that.prices = res.data.data.skus.price_min
						that.normal = res.data.data.skus.items
				    }
				}
			});
		},
		// 选择规格
		 choose(item,idx){
			if(item.available == 0){
			    uni.showToast({
			       title:'不可使用',
			       icon:'none',
			       duration:1500
			    })
			}else{
				this.index = idx
				this.buyTimes = item.name + '(' + item.times + '次)'
				this.prices = item.prices.final_price
				this.bsku_id = item.id
			}
		},
		// 去购买
		gobuy(){
			if(this.bsku_id == ''){
				uni.showToast({
					title:'请选择商品规格',
					icon:'none',
					duration:1500
				})
				return
			}
			this.spellGroup = false;
			var that = this;
			uni.request({
				url: this.http+'order/preview',
				method: 'POST',
				header: {
					authKey: this.authKey
				},
				data: {
					product_id:this.content.product_id,
					sku_id:this.bsku_id,
					assemble_id:this.biId,
					coupon_id:this.coupon_id,
					invite_code:this.invite_code
				},
				success: res => {
					if (res.data.code == 200) {
						let data = res.data.data
						data.item.product.cover = that.static + data.item.product.cover
						uni.navigateTo({
							url: '/pages/order/confirmOrder?data=' + encodeURIComponent(JSON.stringify(data)),
						});
					} else {
						uni.showToast({
							title: res.data.error,
							icon: 'none',
							duration: 1000
						})
					}
				},
			});
		},
		
		shared(){
			if (!this.userInfo){
				this.user = true
				return
			}
			this.specificatbuy = true
		},
		goclose(){
			this.specificatbuy = false
		}
	}
}
